*{margin:0px; padding:0px;}

.bb{
  border:1px solid #ccc;
  display:flex;
  width:600px;
  height:100px;
  position:relative;
}
.bb >h3{
  height: 20px;
  font-size: 14px;
  color:#333;
  padding:10px 5px;
}
.bb > p{
  position:absolute;
  right:3%;
  top:40%;
  font-weight: bold;
  color:#333
}
.bb > p >s{color:#ccc;}

.b_in{
  position:absolute;
  top:35%;
  left:3%;
  border:1px solid #ccc;
  width:100px;
}
.b_in > input[type=button]{
  background-color: #fff;
  border:none;
  width:33.3%;
  padding: 0px;
  margin: 0px;
  font-size: 25px;
  transition: 0.3s;
}
.b_in > span{
  color:#111;
  font-size:16px;
  display:inline-block;
  width:33.3%;
  text-align: center;
}
.b_in > input[type=button]:hover{
  background-color: #ccc;
}

.cc{
  border: 1px solid #ccc;
  width:600px;
  margin-top:10px;
}
.cc > p{
  float: right;
  color:#333;
  font-weight: bold;
  font-size: 14px;
  margin-top: 30px;
  margin-bottom: 10px;

}
.cc > p > span{
  font-size: 32px;
  padding:10px 5px;
}

.cc > button{
  margin: 5px;
  width:590px;
  background-color: purple;
  color:#fff;
  font-size: 20px;
  line-height: 50px;
  border: 1px solid purple;
  display: block;
  border-radius: 6px;
  cursor: pointer;
}
.cc > button:hover{
  background-color: rgb(68, 0, 68);
}
